:root {
  --color-primary: #f7af2b;
  --color-primary-light-1: #fbda9e;
  --color-primary-light-2: #f9c86c;
  --color-primary-dark-1: #956206;
  --color-primary-dark-2: #432c03;
  --heading-font: 'Abril Fatface', cursive;
  --body-font: 'Montserrat', sans-serif;

  --color-succes: #4a90e2;
  --color-succes-light: #ebf3fc;
  --color-delete: #d91e2a;
  --color-preview: #d84b00;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  /* 1rem=10px */
}

body {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--body-font);
  font-size: 1.6rem;
  line-height: 1.7;
  font-weight: 300;
  background: #f2f2f2;
  box-sizing: border-box;
}

.row {
  width: 95%;
  margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Abril Fatface', cursive;
  font-family: var(--heading-font);
}
h2 {
  font-size: 250%;
  text-align: center;
}

h3 {
  font-size: 175%;
}
h4 {
  font-size: 125%;
}
.btns-btn {
  background-color: unset;
  padding: 0.5rem 0.7rem;
  border-radius: 0.2rem;
  /* outline: 0; */
  border: 1px black solid;
  letter-spacing: 0.5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 6px rgba(32, 33, 36, 0.28);
  transform: scale(1);
  transition: all 0.3s ease;
}
.btn-send {
  border: 1px #4a90e2 solid;
  border: 1px var(--color-succes) solid;
  color: #4a90e2;
  color: var(--color-succes);
}
.btns-btn:active,
.btns-btn:focus {
  border: 1px green solid;
  outline: 0;
}
.btn-svg {
  padding: 0 0 0 1.2rem;
  height: 100% !important;
  display: flex;
  align-items: center;
}
.btns-btn:hover .btn-svg {
  transform: scale(1.03);
  fill-opacity: 0.9;
}

.btns-btn:active {
  transform: scale(1.05);
  outline: 0;
}

.btn-preview {
  border: 1px #d84b00 solid;
  border: 1px var(--color-preview) solid;
  color: #d84b00;
  color: var(--color-preview);
}

.btn-delete {
  border: 1px #d91e2a solid;
  border: 1px var(--color-delete) solid;
  color: #d91e2a;
  color: var(--color-delete);
}


.navigation {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  font-weight: 500;
}
.navbar {
  display: flex;
  height: 8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-toggle {
  display: none;
}
.navbar__menu,
.navbar__menu__items {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
.navbar__menu__items {
  list-style: none;
}

.navbar__menu__item {
  position: relative;
  padding: 0 1.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.navbar__menu__items a {
  text-decoration: none;
  color: #fff;
}
.navbar__menu__item:hover a {
  color: #4a90e2;
  font-weight: 500;
}

.navbar__menu__item::after {
  content: '';
  height: 0.2rem;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #4a90e2;
  position: absolute;
  pointer-events: none;
  transform: translateX(-100%);
}

.navbar__menu__item:hover::after,
.navbar__menu__item:active::after {
  -webkit-animation: lineUp 0.8s;
          animation: lineUp 0.8s;
}

.sticky {
  background: rgba(0, 0, 0, 0.63);
  height: 6rem !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: content-box;
  z-index: 9999;
}

@-webkit-keyframes lineUp {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(-50%);
  }
  85% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes lineUp {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(-50%);
  }
  85% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(0%);
  }
}

