.first-article {
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  padding: 0 3rem 2rem 3rem;
}
.first-article__footer-author {
  font-size: 95%;
  font-style: italic;
  display: flex;
  justify-content: space-between;
  padding: 0 2rem;
}
.comments {
  background-color: #f1f1f1;
  box-shadow: unset;
  font-size: 85%;
  width: 100%;
  display: block;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0.5rem 0;
  cursor: pointer;
}
.first-article__body {
  padding: 0 2rem;
}

.comments-container {
  display: flex;
  flex-direction: column;
  background-color: unset;
}

.comments-card {
  display: flex;
  width: 85%;
  justify-content: space-between;
  align-items: center;
}

.comments-card__avatar {
  width: 10rem;
  margin-left: 2rem;
}
.comments-card__avatar-photo {
  width: 10rem;
  height: 10rem;
}
.comments-card__avatar-time {
  text-align: center;
  font-size: 85%;
}
.comments-card__avatar-photo img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.comments-card__body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  width: 75%;
  position: relative;
  background: #fff;
  border: 2px solid #f1f1f1f1;
  border-radius: 0.5rem;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
}
.comments-card__body:after,
.comments-card__body:before {
  right: 100%;
  top: 50%;
  border: solid transparent;
  content: ' ';
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.comments-card__body:after {
  border-color: rgba(255, 255, 255, 0);
  border-right-color: #fff;
  border-width: 10px;
  margin-top: -10px;
}
.comments-card__body:before {
  border-color: rgba(241, 241, 241, 0);
  border-right-color: #f1f1f1f1;
  border-width: 13px;
  margin-top: -13px;
}

.comments-card__body-author {
  height: 3rem;
  font-style: italic;
  padding-left: 2rem;
  color: var(--color-succes);
}

.comments-card__body-actions {
  color: var(--color-succes);
  text-align: end;
  font-weight: 400;
}

.article-card {
  margin: 1rem 0;
  background-color: #fff;
  width: 100%;
  display: flex;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  border-radius: 0.6rem;
  transform: scale(1);
  transition: transform 0.3s ease;
}
.article-card:hover {
  transform: scale(1.051);
}
.article-card__image {
  width: 35%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.article-card__image img {
  border-radius: 0.5rem;
  background-position: center;
  background-size: contain;
  background-repeat: repeat;
}

.article-card__body {
  position: relative;
  cursor: pointer;
  border-radius: 0.3rem;
  width: 65%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  overflow: hidden;
}
.article-card__body-text {
  height: calc(100% - 2rem);
  overflow: hidden;
  padding: 0 2.5rem 0.4rem 1rem;
  font-size: 85%;
}
.article-card__body-footer {
  height: 2rem;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 85%;
  padding-right: 1rem;
  color: var(--color-succes);
}

.svg-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgba(131, 1, 1, 0.2);
  padding-right: 0.2rem;
  height: 100%;
  border-top-right-radius: 0.6rem;
  border-bottom-right-radius: 0.6rem;
}
.svg-icon.delete:hover {
  fill-opacity: 0.41;
  transition: background-color 0.6s ease;
  background-color: rgba(131, 1, 1, 0.4);
  border-top-right-radius: 0.6rem;
  border-bottom-right-radius: 0.6rem;
}

#home {
  height: auto;
  margin-bottom: 5rem;
}
.home-container {
  display: flex;
  flex-direction: column;
}
.home-container h3 {
  text-align: center;
}

.article-cards {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  justify-items: start;
  flex-grow: 1;
}

.article_card_wrapper {
  width: 45%;
}

#post {
  margin-top: 5rem;
  background-color: #fff;
  padding-bottom: 5rem;
  height: auto;
}

.quill {
  min-height: 45vh;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  border-top-left-radius: 0.8rem;
  border-top-right-radius: 0.8rem;
  border-bottom: 1px hidden #fff;
  background-color: #fff;
  border: none;
}

.ql-toolbar {
  border: none;
  border-top-left-radius: 0.8rem;
  border-top-right-radius: 0.8rem;
  background: linear-gradient(45deg, #e0eafc, #cfdef3);
}
.ql-container {
  border: none !important;
}
.editor-preview {
  margin-top: 1rem;
  background-color: #fff;
}

/*  INPUTS STYLING!!!! */
.editor-forms {
  margin-bottom: 1rem;
}
.input_form {
  height: auto;
  display: flex;
  flex-direction: column;
}

.input-form__user-details {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.input-form__user-details .input-text:first-child {
  margin-right: 1rem;
}
.input-text {
  /* box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28); */

  width: 100%;
  height: 6rem;
  position: relative;
  overflow: hidden;
}

.input-text input {
  outline: none;
  width: 100%;
  font-size: 2rem;
  height: 100%;
  padding-left: 2rem;
  border: none;
}
.input-text label {
  position: absolute;
  left: 0;
  border-bottom: 1px black solid;
  bottom: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.input-text input[type='text'] {
  background-color: unset;
}

.input-text label::after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  bottom: -1px;
  border-bottom: 4px var(--color-succes) solid;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}
.input-text__title {
  position: absolute;
  bottom: 5px;
  left: 0px;
  transition: all 0.3s ease;
}
.input-text input:focus + .input-text__label .input-text__title,
.input-text input:valid + .input-text__label .input-text__title {
  transform: translateY(-150%);
  font-size: 1.4rem;
  color: var(--color-succes);
}

.input-text input:focus + .input-text__label::after,
.input-text input:valid + .input-text__label::after {
  transform: translateX(0);
}
.input-form__article {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.input-form__btns {
  display: flex;
  margin-top: 0.5rem;
  width: 100%;
  justify-content: flex-end;
  height: auto;
}

.h2-modal {
  width: 100%;
}
.close-modal {
  position: absolute;
  right: 2rem;
  top: 4rem;
}

#contact {
  height: 100vh;
  background: url(/ls-blog/static/media/footer.8ffc6e2d.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-form {
  width: 60rem;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  padding: 0.5rem;
}
textarea {
  width: 100%;
  resize: none;
  outline: 0;
  padding: 1rem 2rem;
  height: 20rem;
  font-style: italic;
  border: none;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
}

